import React, {Suspense} from 'react';
import {Navigate, Route, Routes} from "react-router-dom";
import News from "../omponents/news/News";
import Music from "../omponents/music/Music";
import Settings from "../omponents/settings/Settings";

import Preloader from "../omponents/preloader/Preloader";

const ProfileContainerLazy = React.lazy(() => import('../omponents/profile/ProfileContainer'))
const MessagesContainerLazy = React.lazy(() => import('../omponents/messages/MessagesContainer'))
const UsersContainerLazy = React.lazy(() => import('../omponents/users/UsersContainer'))
const LoginLazy = React.lazy(() => import('../omponents/login/Login'))
const Error404Lazy = React.lazy(() => import('../omponents/error404/Error404'))

export const Pages = () => {

    return (
        <Routes>
            <Route path={'/'} element={<Navigate to={'/profile'}/>}/>

            <Route path={'/news'} element={<News/>}/>
            <Route path={'/music'} element={<Music/>}/>
            <Route path={'/settings'} element={<Settings/>}/>

            <Route path={'/users'} element={
                <Suspense fallback={<Preloader/>}>
                <UsersContainerLazy/>
                </Suspense>
            }/>

            <Route path={'login'} element={
                <Suspense fallback={<Preloader/>}>
                    <LoginLazy/>
                </Suspense>
            }/>

            <Route path={'/profile/:userId?'} element={
                <Suspense fallback={<Preloader/>}>
                    <ProfileContainerLazy/>
                </Suspense>
            }/>

            <Route path={'/messages'} element={
                <Suspense fallback={<Preloader/>}>
                    <MessagesContainerLazy/>
                </Suspense>
            }/>

            <Route path={'/messages/:id'} element={
                <Suspense fallback={<Preloader/>}>
                    <MessagesContainerLazy/>
                </Suspense>
            }/>

            <Route path={'/*'} element={
                <Suspense fallback={<Preloader/>}>
                <Error404Lazy/>
                </Suspense>
                }/>
        </Routes>
    );
};
